	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>Virtual Gestures is a series of experiments that examine the relationships and tensions between the digital and physical. Screen-based gestures are performed physically with these materialized digital artifacts as a form of reverse skeumorphism. In carrying out this translation, it is hoped that we will uncover a deeper understanding of these interactions and the digital environment, revealing the gap between how we experience these two distinct yet intertwined worlds. </p>\n<p><a href=\"virtualgestures.co\">Click here</a> to view the project.</p>","frontmatter":{"title":"Virtual Gestures","graduate":"Soojin Kim","category":"Experiential Design","category2":"Installation","category3":"Web Design","image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/the-next-website/static/virtual-gestures-be3f415f5f051f50b8b1e592632698e0-a7a62.jpg","srcSet":"/the-next-website/static/virtual-gestures-be3f415f5f051f50b8b1e592632698e0-5a1a9.jpg 375w,\n/the-next-website/static/virtual-gestures-be3f415f5f051f50b8b1e592632698e0-0908c.jpg 750w,\n/the-next-website/static/virtual-gestures-be3f415f5f051f50b8b1e592632698e0-a7a62.jpg 1500w,\n/the-next-website/static/virtual-gestures-be3f415f5f051f50b8b1e592632698e0-9f250.jpg 1800w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}},"projectsInCategory":{"edges":[{"node":{"frontmatter":{"title":"Virtual Gestures","graduate":"Soojin Kim"}}}]},"projectByStudent":{"edges":[{"node":{"frontmatter":{"title":"Offline Texts","graduate":"Soojin Kim"}}}]}},"pathContext":{"slug":"/work/virtual-gestures/","name":"/Virtual Gestures/","title":"Virtual Gestures","graduate":"/Soojin Kim/","category":"/Experiential Design/"}}

